import { Component, Prop, h, Host } from '@stencil/core';
import { classlist } from '../../utils/classlist';

const containerVariantsConfig = {
    variants: {
        shadow: {
            none: 'shadow-none',
            sm: 'shadow-sm',
            lg: 'shadow-lg',
        },
    },
};

const contentVariantsConfig = {
    variants: {
        padding: {
            true: 'px-16 pt-8 pb-16',
            false: 'p-0',
        },
        overflow: {
            true: 'overflow-y-auto',
            false: '',
        },
    },
};

const headerVariantsConfig = {
    variants: {
        sticky: {
            true: 'bg-card/80 sticky top-0 backdrop-blur',
            false: '',
        },
    },
};

const footerVariantsConfig = {
    variants: {
        sticky: {
            true: 'bg-card/80 sticky bottom-0 backdrop-blur',
            false: '',
        },
    },
};

/**
 * @category Layout
 * @description A flexible container component for organizing content with header, body, and footer sections. Features sticky headers/footers, configurable padding, shadows, and overflow handling.
 *
 * @slot card-header - Placed in the header of the card, above the header actions & before the icon and title
 * @slot card-header-actions - Placed in the header of the card, below the header content & after the icon and title
 * @slot - Placed in the content area of the card.
 * @slot card-footer - Placed below the content of the card
 */
@Component({
    tag: 'at-card',
    shadow: false,
})
export class AtCardComponent {
    /**
     * Title of the card.
     */
    @Prop()
    card_title?: string;

    /**
     * Subtitle of the card, placed below title.
     */
    @Prop()
    subtitle?: string;

    /**
     * Content of the card, placed below title, and subtitle.
     */
    @Prop()
    content?: string;

    /**
     * Enables overflow on the content area. If false overflow will be applied on the host.
     */
    @Prop()
    overflow_content?: boolean = false;

    /**
     * Display header persistently at top of card.
     */
    @Prop()
    sticky_header?: boolean = true;

    /**
     * Display footer persistently at bottom of card.
     */
    @Prop()
    sticky_footer?: boolean = true;

    /**
     * Apply or remove padding form the card content area.
     */
    @Prop()
    padding?: boolean = true;

    /**
     * Box-shadow around card.
     */
    @Prop()
    shadow?: 'none' | 'sm' | 'lg' = 'none';

    render() {
        const getContainerClassname = classlist(
            'relative z-1 border border-light bg-card flex flex-col overflow-hidden rounded-lg',
            containerVariantsConfig,
        );

        const containerClassname = getContainerClassname({
            shadow: this.shadow,
        });

        const getHeaderClassname = classlist(
            'hide-empty relative z-2 flex items-center justify-between gap-8 rounded-t-lg p-16',
            headerVariantsConfig,
        );

        const headerClassname = getHeaderClassname({
            sticky: this.sticky_header,
        });

        const getContentClassname = classlist(
            'relative flex flex-auto flex-col',
            contentVariantsConfig,
        );

        const contentClassname = getContentClassname({
            padding: this.padding,
            overflow: this.overflow_content,
        });

        const getFooterClassname = classlist(
            'hide-empty z-index-10 p-16',
            footerVariantsConfig,
        );

        const footerClassname = getFooterClassname({
            sticky: this.sticky_footer,
        });

        return (
            <Host class={containerClassname}>
                <div class={headerClassname}>
                    <slot name="card-header" />

                    {(this.card_title || this.subtitle) && (
                        <div class="flex flex-col">
                            {this.card_title && (
                                <h4
                                    data-name="card-title"
                                    class="text-h4 font-medium"
                                >
                                    {this.card_title}
                                </h4>
                            )}
                            {this.subtitle && (
                                <h5
                                    class="text-light text-sm"
                                    data-name="card-subtitle"
                                >
                                    {this.subtitle}
                                </h5>
                            )}
                        </div>
                    )}

                    <slot name="card-header-actions" />
                </div>

                <div
                    class={contentClassname}
                    data-name="card-content"
                >
                    {this.content}
                    <slot />
                </div>

                <div class={footerClassname}>
                    <slot name="card-footer" />
                </div>
            </Host>
        );
    }
}
